@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  min-height: 100vh;
  background: #3498db;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.wrapper {
  height: 260px;
  max-width: 410px;
  background: #fff;
  border-radius: 7px;
  padding: 16px 25px;
  transition: height .2s;
}

.wrapper.active {
  height: 550px;
}

header h1 {
  font-size: 21px;
  font-weight: 500;
}

header p {
  color: #474747;
  margin-top: 5px;
  font-size: 16px;
}

.wrapper .form {
  margin: 20px 0;
}

.form :where(input, button) {
  width: 100%;
  height: 55px;
  border: none;
  border-radius: 5px;
  outline: none;
}

.form input {
  border: 1px solid #999;
  padding: 0 17px;
  font-size: 18px;
}

.form button {
  color: #fff;
  cursor: pointer;
  background: #3498db;
  margin-top: 20px;
  font-size: 17px;
}

.wrapper .qr-code {
  opacity: 0;
  pointer-events: none;
  border: 1px solid #ccc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 33px 0;
  display: flex;
}

.wrapper.active .qr-code {
  opacity: 1;
  pointer-events: auto;
  transition: opacity .5s .5s;
}

footer {
  color: #fff;
  width: 410px;
  height: 100%;
  text-align: center;
  background: #000;
  border-radius: 5px;
  position: relative;
}

